import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { UserService } from '../user.service';


@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css']
})
export class UserComponent implements OnInit {

  constructor(private userService: UserService) {

    this.searchResultEvent = new EventEmitter<String>();
  }

  ngOnInit() {

  }
  receivedData : String;
  loading: boolean;
  firstUser: string;

  @Output() searchResultEvent: EventEmitter<String>;

  searchUser(name: string) : void {

    this.loading = true;
    this.firstUser = "";

    let pr = this.userService.getUser(name);
    pr.subscribe(
      data => {
        console.log(JSON.stringify(data));
        this.loading = false;
        if (data["results"].length > 0) {
          this.firstUser = data["results"][0].name;
        }
    //    this.searchResultEvent.emit(this.firstUser);
      },
       error  => {
      //   this.loading = false;
      //   this.firstUser = error.status;
      //   console.log(error);
      }
    );
  }



}
